.mainContainer {
    display: flex;
    padding: 14px;
    min-height: calc(100vh - 128px);
}

.content {
    margin: $view-margin;
    width: 100%;
}

.advertList {
    display: flex;
    flex-flow: wrap;
    &__item {
        width: 23.5%;
        height: 320px;
        border: 1px solid $main-border;
        margin: 0 0 16px 8px;
        position: relative;
        &Image {
            height: 50%;
            & img {
                height: 100%;
                width: 100%;
                object-fit: cover;
                object-position: top;
            }
        }
        &Info {
            text-align: center;
            height: 50%;
            position: relative;
            & h2 {
                text-align: center;
                font-size: $font-size-large;
                padding: 6px;
                position: relative;
                top: 15px;
            }
            &Price {
                position: absolute;
                left: 0;
                right: 0;
                padding: 10px 0;
                background: lighten($font-color, 15%);
                color: $font-color-white;
                bottom: 30%;
            }
            span {
                position: absolute;
                bottom: 8%;
                right: 5%;
                font-size: 14px;
            }
        }
        & a {
            position: absolute;
            top: 0;
            width: 100%;
            height: 100%;
        }
    }
}

.advert {
    display: flex;
    &Item {
        width: 50%;
        margin: 0 6px;
    }
    &__back {
        background: $advert-back-bg;
        padding: 7px;
        display: flex;
        a {
            background: $advert-back;
            padding: 6px 12px;
            border-radius: 1px;
            color: $font-color;
        }
    }
    &__title {
        margin-top: 20px;
        h1 {
            font-weight: 500;
            font-size: $font-size-extra-large;
        }
    }
    &__price {
        margin-top: 30px;
        display: flex;
        h2 {
            font-weight: 500;
            width: 30%;
        }
        p {
            width: 70%;
        }
    }
    &__desc {
        margin-top: 10px;
        display: flex;
        h2 {
            font-weight: 500;
            width: 30%;
        }
        p {
            width: 70%;
        }
    }
    &__image {
        height: 450px;
        img {
            width: 100%;
            height: 100%;
            object-fit: cover;
            object-position: top;
        }
    }
}

.login-input {
    outline: none;
    height: 28px;
    line-height: 30px;
    padding-left: 10px;
    box-sizing: border-box;
    width: 100%;
    font-size: 15px;
}

.login,
.register {
    position: relative;
    top: 80px;
    width: 250px;
    margin: 0 auto;
    background: lighten($font-color, 40%);
    padding: 20px;
    &__title {
        display: flex;
        justify-content: space-between;
        font-size: $font-size-extra-large;
        margin-bottom: 25px;
        img {
            height: $font-size-extra-large;
        }
    }
    &__form {

    }
    &__formItem {
        margin-bottom: 15px;
    }
    &__formInput {
        input {
            @extend .login-input;
        }
        span {
            margin-top: 6px;
            display: block;
            color: $error-message;
        }
    }
    &__formSubmitButton {
        margin-bottom: 25px;
        text-align: center;
        button {
            @extend .loginButton;
        }
    }
    &__formRegisterLink,
    &__formLoginLink {
        text-align: center;
        background: $content-bg;
        padding: 12px 0;
        p {
            margin-bottom: 8px;
        }
        a {
            text-decoration: underline;
            color: $font-color-dark;
        }
    }
}